    import {  
        mapState
    } from 'vuex';
	
	import shopdesc from "@/components/shopdesc.vue";
	import searchinput from "@/components/search-input.vue";
	import shoppicker from "@/components/shop-pickers.vue";
	import awap from "../../js/amap-wx.js";
	import gd from "@/js/global-data.js"
	
	export default {
		components: {
			searchinput,
			shoppicker,
			shopdesc,
		},
		data() {
			return {
				content: '',
				address: '',
				userInfo: {},
				permission: ["phoneNumber", "location", "userInfo"],
			}
		},
		onLoad() {
			var that = this
			this.$store.commit("stateReport")
			if(!this.hasLogin){
				uni.hideTabBar({
					animation: true,
					success() {
						uni.showModal({
							confirmText: "注册登录",
							cancelText: "游客使用",
							content: "你尚未登陆",
							success(res){
								if(res.cancel){
									that.$store.dispatch.actionLogin;
									uni.showTabBar({
										animation: true
									})
								}
								else{

								}
							}
						})
					}
				})				
			}
		},
        computed: mapState(['hasLogin']),
		methods: {
			locationChange: function(){
				uni.navigateTo({
					url: "../index/location/location"
				})
			},
			/* 用户信息许可 电话许可 地址许可 */
			getAddress: function(){
				var that = this
				uni.getLocation({
					success(res) {
						var newAwap = new awap.AMapWX({key:'c1b7100505882e2a610dd85fc14798a3'});
						newAwap.getRegeo({
							success(res){
								that.address = res[0].regeocodeData.addressComponent.city;
							},
							fail(err){
								console.log(err);
							}
						})	
					}
				})
	
			},
		}
	}
